<template>
    <b-list-group-item class="d-flex justify-content-between align-items-center">
        <div class="d-flex">
            <b-button pill size="sm" 
            class="custom-btn" 
            variant="primary" 
            @click="addItemCount" 
            :class="{active: count > 0}"
            v-if="editMode==false"
            >
            <b-icon icon="plus"></b-icon>
            </b-button>

            <b-button pill size="sm" 
            class="drag-btn" 
            variant="primary"
            v-if="editMode==true"
            >
            <b-icon icon="arrows-move"></b-icon>
            </b-button>
            <div class="item">
                <span>{{item}}</span>

                <span class="p-2">
                    <button class="no-style-button" @click="toggleFavourite">
                        <b-icon v-if="favourite == true" icon="star-fill" variant="warning"></b-icon>
                        <b-icon v-else icon="star" variant="secondary"></b-icon>
                    </button>
                </span>
                
                
                
                <span v-if="section !=undefined"><small>  ({{section}})</small></span>
            </div>
            
        </div>

        <div class="d-flex">
            <div class="item" :class="{invisible: count==0}" v-if="editMode==false">{{count}}</div>
            <button class="custom-del-btn" @click="removeItemCount" :class="{invisible: count==0}" v-if="editMode==false"><b-icon icon="dash"></b-icon></button>  
            
            <b-button pill size="sm" 
            class="del-btn" 
            variant="primary"
            v-if="editMode==true"
            @click="removeItem"
            >
            <b-icon icon="trash-fill"></b-icon>
            </b-button>
        </div>
    </b-list-group-item>

</template>


<script>
export default {
  name: 'ShoppingRow',
  props: {
    item: String,
    count: Number,
    editMode:Boolean,
    section:String,
    favourite:Boolean
  },
  data(){
      return{
      
      }
  },
  methods:{
      addItemCount(){
          this.$emit("addItemCount",this.item)
      },
      removeItemCount:function(){
          this.$emit("removeItemCount",this.item)
      },
    removeItem:function(){
          this.$emit("removeItem",this.item)
      },
    toggleFavourite:function(){
          this.$emit("toggleFavourite",this.item)
    }
  }
}
</script>

