
export default {
    updatePantryList(state,list){
        // state.pantry_list = JSON.parse(JSON.stringify(list))
        state.pantry_list = list
    },

    addFoodToPantryList(state, {newFood:newFood, index:index}) {
        state.pantry_list[index].items.push({
          name: newFood,
          count: 0,
          purchased: false,
          favourite:false,
        })
    },

    addSectionToPantryList(state, title){
      let section = {
        title: title,
        items: [],
      };
      state.pantry_list.push(section);
    },

    addItemCount(state, itemName){
      state.pantry_list.forEach((section) => {
        section.items.forEach((item) => {
          if (item.name == itemName) {
            item.count++;
          }
        })
      })
    },

    removeItemCount(state, itemName) {
      state.pantry_list.forEach((section) => {
        section.items.forEach((item) => {
          if (item.name == itemName) {
            if (item.count > 0) {
              item.count--;
            }
          }
        });
      });
    },

    removeItem(state, itemName) {
      state.pantry_list.forEach((section) => {
          const index = section.items.map(item=>item.name).indexOf(itemName)
          if(index >-1){
            section.items.splice(index,1)
          }
      })
    },

    removeSection(state, index) {
      if(index >-1){
        state.pantry_list.splice(index,1)
      }
    },

    toggleFavourite(state,itemName){
      
      state.pantry_list.forEach((section) => {
        const index = section.items.map(item=>item.name).indexOf(itemName)
        if(index >-1){
          if(section.items[index].favourite == undefined){
            this._vm.$set(section.items[index],'favourite',true)
          }else{
            this._vm.$set(section.items[index],'favourite',!section.items[index].favourite)
          }
        }
    })
    },

    toggleSelected(state, itemName) {
        state.pantry_list.forEach((section) => {
          section.items.forEach((item) => {
            if (item.name == itemName) {
              item.purchased = ! item.purchased;
            }
          });
        })
      },

}

