<script>
// @ is an alias to /src

import { mapMutations, mapState } from "vuex";
import ShoppingRow from "@/components/ShoppingRow.vue";
import TabBar from "@/components/TabBar.vue"

let windowHeight = 0;

export default {
  name: "Pantry",
  components: {
    ShoppingRow,
    TabBar,
  },
  data() {
    return {
      editMode: false,
      sectionIndex:-1,
      newFood: [],
      newSection: "",
      searchText: "",
      keyBoardVisible:false,
      sideBarVisible:false,
      itemToRemove:0,
      sectionToRemove:0,
      countDownAlert:0,
      dismissSecs:1,
      showCountDown:false
    };
  },
  methods: {
    test_print() {
      return this.test_value;
    },
    addFood(idx) {
      if (this.newFood[idx].length > 0) {
        
        let fullItemList = this.pantry_list
                            .map(section=>section.items)
                            .flat()
                            .map(item=>item.name)

          if(fullItemList.includes(this.newFood[idx])){
          alert("Error - food already included")
          this.$set(this.newFood,idx,"")
        }else{
          this.addFoodToPantryList({newFood:this.newFood[idx], index:idx})
          this.newFood[idx] = ""
        }
        
      }
    },
    addSection() {

      if (this.newSection.length > 0) {

        if(this.pantry_list.map((section)=>section.title).includes(this.newSection)){
          alert("Error - section already included")
          this.newSection = ""
        }else{
        this.addSectionToPantryList(this.newSection)
        this.newSection = "";

        this.$nextTick(()=>{
          let pantry = document.querySelector(".pantry")
          let nodeList = pantry.querySelectorAll(".new-food")
          let nl = nodeList.length
          nodeList[nl-1].focus()
        })
        }


      }
    },
    updateLocalStorage() {
      localStorage.setItem("pantry_list", JSON.stringify(this.pantry_list));
    },
    editToggleMode() {
      this.editMode = !this.editMode
    },
    copyJSONtoClipBoard() {
      this.countDownAlert = this.dismissSecs
      navigator.clipboard.writeText(JSON.stringify(this.pantry_list)) //async

    },
    itemFilter(value) {
      if (this.searchText.length > 0) {
        let r = new RegExp(this.searchText, "i");
        return value.filter((item) => item.name.match(r));
      }
      return value;
    },
    removeItemCheck(e){
      this.itemToRemove = e
      this.$bvModal.show("modal-delete")
      //modal-delete
    },
    removeSectionCheck(e){
      this.sectionToRemove = e
      this.$bvModal.show("modal-delete-section")
    },
    ...mapMutations([
      "updatePantryList",
      "addFoodToPantryList",
      "addItemCount",
      "removeItemCount",
      "addSectionToPantryList",
      "removeItem",
      "removeSection",
      "toggleFavourite"
      ]),

    reset_data() {
      // this.updatePantryList(JSON.parse(JSON.stringify(this.defaultList)))
      this.updatePantryList("");
      
      this.updateLocalStorage();
      this.updatePantryList(JSON.parse(JSON.stringify(this.defaultList)));
    },
    // returnhome(){
    //   window.scroll(0,0)
    // },
    nextSection(){
      // this.addFav()
      if(this.sectionIndex < this.pantry_list.length - 1){
        this.sectionIndex++
      }
    },
    previousSection(){
      if(this.sectionIndex > -1){
        this.sectionIndex--
      }
    },
    gotoSection(index){
      this.sideBarVisible = false
      this.sectionIndex = index
    }
  },
  beforeDestroy() {
    this.updateLocalStorage();
  },
  mounted() {
    
    if (this.pantry_list.length == 0) {
      let localList = localStorage.getItem("pantry_list");

      if (localList != "null" && localList != null) {
        this.updatePantryList(JSON.parse(localList));
      }

      if (this.pantry_list.length == 0) {
        this.updatePantryList(JSON.parse(JSON.stringify(this.defaultList)));
      }
    }
  },

  beforeMount() {
    window.addEventListener("beforeunload", this.updateLocalStorage);
    
  //Code to decide if buttons need hiding
  if(windowHeight == 0){
        windowHeight = window.innerHeight
    }
    window.addEventListener("resize", ()=>{
    

      if(window.innerHeight < windowHeight){
        this.keyBoardVisible = true
      }else{
        this.keyBoardVisible = false
      }
    })

  },
  computed: {
    ...mapState(["defaultList", "pantry_list"]),
    showHideJSON() {
      if (this.showJSON == true) {
        return "Hide JSON";
      } else {
        return "Show JSON";
      }
    },
    combinedPantryList(){
      let outputArray = []
      this.pantry_list.forEach((section)=>{
        section.items.forEach((item)=>{
          let o={
            name:item.name,
            section:section.title,
            count:item.count
          }
          outputArray.push(o)
        })
      })
      return outputArray
    },
    nextSectionName(){
      let nextIndex = this.sectionIndex

      if(nextIndex < this.pantry_list.length - 2){
        nextIndex++
        console.log(nextIndex)
        return this.pantry_list[nextIndex].title
      }else{
        return "End of list"
      }
    },
    previousSectionName(){
      let prevIndex = this.sectionIndex
      if(prevIndex == 0){
        prevIndex--
        return "Favourites"
      }


      if(prevIndex > 0){
        prevIndex--
        console.log(prevIndex)
        return this.pantry_list[prevIndex].title
      }else{
        return "Start of list"
      }
    },
    shoppingListCount(){

      let a = this.pantry_list
              .map(section=>section.items)
              .flat()
              .filter(item=>item.count > 0)

      return a.length
    },
    sectionListCount(){
      // Count how many items are in a section
      return this.pantry_list
              .map(section=>section.items
              .reduce((acc,curr)=>acc+ (curr.count > 0 ? 1 : 0),0)
              )
    },
    favouriteList(){
      return this.pantry_list
              .map(section=>section.items)
              .flat()
              .filter(item=>item.favourite == true)
      
    }
  },
};
</script>

